<template>
  <div class="particulars">
    <div
      class="m-bg"
      :style="{'background-image': 'linear-gradient(to bottom, rgba(208,208,208, 0), rgba(208,208,208, .85) 75%, rgba(208,208,208, 1) 80%),url(' + particularsData.data.cover + ')'}"
    ></div>
    <div>
      <van-nav-bar :title="particularsData.data.name" left-text="返回" left-arrow @click-left="back" />
    </div>
    <div class="particulars-box">
      <div class="infor clearfix">
        <div class="fl">
          <h3 class="one-text">{{particularsData.data.name}}</h3>
          <p class="one-text">作者: {{particularsData.data.author}}</p>
          <p class="one-text">类型: {{particularsData.data.tag}}</p>
          <p class="one-text">状态: {{particularsData.data.status}}</p>
          <p class="one-text">最新更新: {{particularsData.data.time}}</p>
          <van-button
            round
            color="linear-gradient(60deg, orangered 30%  , #e41518 60% )"
            :text="look ? '继 续 阅 读' : '开 始 阅 读'"
            @click="reading()"
          ></van-button>
        </div>
        <div class="fr">
          <div class="pho">
            <img class="auto-img" :src="particularsData.data.cover" alt />
          </div>
          <van-button
            round
            color="linear-gradient(60deg, orangered 30%  , #e41518 60% )"
            ref="enshrine"
            :text="text ? '已加入书架' : '加 入 书 架'"
            @click="enshrine()"
            :disabled="text"
          ></van-button>
        </div>
      </div>
      <div class="infor-p">
        <p class="three-text" :style="{display}">{{particularsData.data.introduce}}</p>
        <!-- 箭头 -->
        <div class="arrow" @click="changeDisplay">
          <img class="auto-img" src="../assets/image/xia.png" alt />
        </div>
      </div>
      <div class="particulars-list clearfix">
        <div class="p-lists fl">
          <div class="list fl" v-for="(item,index) in list " :key="index" @click="Goto(index)">
            <p class="one-text">{{item.num}}</p>
          </div>
        </div>
        <div class="num fl">
          <van-pagination
            v-model="pagination.currentPage"
            :total-items="100"
            :show-page-size="3"
            :items-per-page="pagination.tems"
            :page-count="pagination.count"
            force-ellipses
            color="#e4393c"
            @change="Page"
          ></van-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("particularsModule");

export default {
  name: "particulars",
  data() {
    return {
      list: [],
      mhurl: [],
      url: [],
      i: 1,
      text: false,
      look: false
    };
  },

  computed: {
    ...mapState(["display", "pagination", "particularsData"])
  },

  methods: {
    ...mapMutations([
      "changeDisplay",
      "paginationSet",
      "getparticularsData",
      "resetState"
    ]),

    Page(i) {
      // console.log(" i ==>> ", i);
      this.i = i;
      let index = (i - 1) * this.pagination.tems;
      this.list = [...this.pagination.list].splice(index, this.pagination.tems);
    },

    // 返回上一步
    back() {
      this.$router.push({ name: "videos" });
    },

    Goto(index) {
      let i = index;
      // console.log("i =>> ", i);
      this.$router.push({
        name: "contents",
        query: {
          mhurl: this.mhurl,
          index: index + (this.i - 1) * this.pagination.tems,
          mhUrl: this.url
        }
      });
    },

    enshrine(index) {
      //获取用户登录状态
      let userStatus = localStorage.getItem("user");

      // console.log("userStatus ==>> ", userStatus);

      if (userStatus) {
        userStatus = JSON.parse(userStatus);

        if (!userStatus.isLogin) {
          return this.$router.push({ name: "login" });
        }

        this.text = true;
        let data = this.particularsData;
        let pho = {
          img: data.data.cover,
          name: data.data.name,
          mhName: data.list[0].num,
          mhUrl: this.$route.params.url,
          conUrl: data.list[0].url,
          i: this.i
        };

        // console.log("index ==>> ", index);

        let likeMovieData = localStorage.getItem("enshrineData");
        likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : [];

        // console.log("likeMovieData ==>> ", console.log(likeMovieData));

        localStorage.setItem(
          "enshrineData",
          JSON.stringify([...likeMovieData, pho])
        );
      } else {
        this.$router.push({ name: "login" });
      }
    },

    reading() {
      this.look = true;
      this.$router.push({
        name: "contents",
        query: {
          mhurl: this.mhurl,
          index: 0,
          mhUrl: this.url
        }
      });
    }
  },

  created() {
    //重置state
    this.resetState();

    //根据用户登录状态，判断该电影是否被该用户收藏
    let userStatus = localStorage.getItem("user");
    // console.log("userStatus ==>> ", userStatus);
    
    let isActive = userStatus && JSON.parse(userStatus).isLogin;
    //  console.log("isActive ==>> ", isActive);

    //如果用户登录
    if (isActive) {

       //开启加载提示
    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });

      let username = JSON.parse(userStatus).username;

      let likeMovieData = localStorage.getItem("enshrineData");
      likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : [];
      // console.log("likeMovieData ==>> ", likeMovieData);

      let url;
      if (this.$route.query.url == undefined) {
        url = this.$route.params.url;
        // console.log("url ==>> ", url);
      } else {
        url = this.$route.query.url;
        // console.log("url ==>> ", url);
      }

      this.url = url;
      // console.log("this.url ==>> ", this.url);

      for (let i = 0; i < likeMovieData.length; i++) {
        if (likeMovieData[i].mhUrl == url) {
          this.text = true;
          break;
        }
      }
      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?mhurl1=" + url
      }).then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("particularsModule/paginationSet", result);
        this.list = [...result.data.list].splice(0, this.pagination.tems);
        this.$store.commit("particularsModule/getparticularsData", result.data);

        this.mhurl = result.data.list;
        // console.log("this.mhurl ==>> ", this.mhurl);

        //  console.log("conUrl ==>> ", result.data.list[0].url);

        this.$toast.clear();
      })
      .catch(err => {
        // console.log("err ==>> ", err);
        this.$toast.clear();
      });
    }else {
       this.$toast.clear();
      //未登录
      this.$router.push({ name: "register" });
     
    }
  }
};
</script>
<style>
.particulars .van-pagination__item {
  color: #e4393c;
}
.particulars .van-pagination__item--active {
  color: white;
  background: #e4393c;
}
.particulars .van-pagination__item:active {
  background: #e4393c;
}
.particulars .van-nav-bar .van-icon {
  color: #e4393c;
}
.particulars .van-nav-bar__text {
  color: #e4393c;
  font-size: 16px;
}
.particulars .van-nav-bar__title {
  font-size: 20px;
  color: #e4393c;
}
</style>
<style lang="less" scoped>
.m-bg {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  filter: blur(5px);
  // opacity: .95;
}
.particulars-box {
  width: 100%;
  position: fixed;
  top: 46px;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 8px;
  box-sizing: border-box;
  z-index: 2;
  overflow-y: auto;
  .infor {
    width: calc(~"100% - 22px");
    // height: 150px;
    border-radius: 10px;
    padding: 10px;
    margin-top: 40px;
    background: white;
    .fl {
      width: 50%;
      h3 {
        width: 100%;
        font-size: 20px;
        color: #e4393c;
        display: inline-block;
      }
      p {
        width: 100%;
        font-size: 16px;
        margin-bottom: 8px;
      }
      .van-button--normal {
        font-size: 18px;
        margin: 0 auto;
        display: block;
      }
    }
    .fr {
      width: 50%;
      height: 100%;
      .pho {
        width: 145px;
        height: 200px;
        border-radius: 10px;
        overflow: hidden;
        position: absolute;
        right: 25px;
        top: 8px;
        background-size: cover;
      }
      .van-button--normal {
        font-size: 18px;
        margin: 0 auto;
        display: block;
        position: absolute;
        top: 220px;
        right: 30px;
      }
    }
  }
  .infor-p {
    width: calc(~"100% - 22px");
    // height: 100px;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
    background: white;
    p {
      font-size: 18px;
      line-height: 25px;
    }
    .arrow {
      width: 28px;
      height: 18px;
      margin: 10px auto 0;
    }
  }
  .particulars-list {
    width: 100%;

    background: white;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 5px;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
    .p-lists {
      width: 100%;
      min-height: 150px;
    }
    .list {
      width: 42%;
      background: #9999;
      border-radius: 10px;
      padding: 8px;
      font-size: 16px;
      margin: 5px;
    }
    .num {
      width: 100%;
      margin: 10px 0;
    }
  }
}
</style>